.CardList {
  display: block;
  position: relative;
  width: 100%;
  padding-bottom: 10px;
}

/* CARDS SECTION */

.Cards li {
  display: block;
  position: relative;
  width: 100%;
  padding-bottom: 10px;
}

.Card {
  position: relative;
  background-color: #fafafa;
  height: 100px;
  width: 100%;
  margin-bottom: 12px;
  border-radius: 4px;
  border-top: 1px solid white;
  border-left: 1px solid white;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  z-index: 1;
}

/* CARDS CONTENT SECTION */
.ContainText {
  position: absolute;
  width: 76%;
  color: #1976d2;
  top: -2px;
  left: 90px;
  text-align: justify;
}

.ContainText p {
  position: absolute;
  top: 30px;
  font-size: 1rem;
  color: #202020;
}

.Face {
  position: relative;
  height: 70px;
  width: 70px;
  top: 10px;
  left: 10px;
}
.Face img {
  border-radius: 50%;
}

.Face h2 {
  position: relative;
  left: 3px;
  top: 20px;
  transform: rotate(90deg);
}

.Extra {
  position: relative;
  width: 90%;
  top: 7px;
  margin: auto;
  color: #606060;
  font-weight: light;
}

.Disappear {
  position: relative;
  width: 90%;
  height: 40%;
  top: 5px;
  margin: auto;
  opacity: 0;
  color: #202020;
  font-weight: thin;
}

.Appear {
  animation: appear 1.2s ease;
  animation-fill-mode: forwards;
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Transform {
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.Transform-active {
  background-color: #fafafa;
  height: 300px;
  width: 100%;
  box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.2);
  border-top: 1px solid white;
  border-left: 1px solid white;
}

@media (max-width: 499px) {
  .ContainText p {
    font-size: 0.7rem;
  }
  .ContainText h3 {
    font-size: 0.85rem;
  }
 
}
